<template>
  <div class="">
    <div class="search-header-1">
      <router-link to="fanHui" class="iconfont">&#xf016e;</router-link>
      <h2>搜索</h2>
    </div>
    <div class="search-header-2">
      <div class="search-header-3">
        <span class="srarchNav" @click="searchFc=!searchFc">
          <img :src="currentSrc" alt="">
        </span>
        <a href="JavaScript:;" @click="searchFc=!searchFc">▼</a>
        <input class="searchText" type="search" placeholder="请输入搜索关键词">
      </div>
    </div>



    <div class="search-header-4" v-show="searchFc" @click.self="hideFc()">
        <div class="search-xBox">
          <span>▲</span>
            <ul class="search-list">
              <li v-for="item in list" >
                <img @click="list_1" :src="item.src" alt=""><br/>
                <a href="JavaScript:;">{{item.title}}</a>
              </li>
            </ul>
        </div>
    </div>
  </div>

</template>

<script>
  export default {
      data(){
          return{
            searchFc:false,
            currentSrc:'../../../static/img/search_icons_03.png',
            list:[
              {src:"../../../static/img/search_icons_03.png",title:"新闻"},
              {src:"../../../static/img/search_icons_05.png",title:"神马"},
              {src:"../../../static/img/search_icons_01.png",title:"百度"}
            ]
          }
      },
    methods:{
      hideFc(){
        this.searchFc=false;
      },
      list_1(ev){
//          console.log(ev.target.src);
        this.currentSrc=ev.target.src;
//        console.log(index);
        this.searchFc=false;
      }

    }
  }
</script>

<style scoped lang="scss">
.search-header-1{
  width: 100%;
  height: 2.2rem;
  background: #D63C39;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  a{
    position: absolute;
    top: 0.5rem;
    left: .5rem;
    color: white;
    font-size: 1rem;
  }
  h2{
    color: white;
    font-size: 1rem;
    font-weight: bold;
  }
}
.search-header-2{
  width:100%;
  height: 2rem;
  background: #C9C9C9;
  display: flex;
  justify-content: center;
  align-items: center;
}
  .search-header-3{
    width:95%;
    height: 1.5rem;
    background: white;
    border-radius: 3px;
    display: flex;
    justify-content: space-between;
    .searchText{
      display:block;
      width:88%;
      height: 100%;
      font-size: 0.5rem;
      line-height: 2rem;
      border-radius: 3px;
    }
    .srarchNav{
      img {
        width: 1rem;
        height: 1rem;
        margin: 0.2rem;
      }

    }
    a{
      font-size: 0.3rem;
      color: #ddd;
      display: inline-block;
      margin-top: 0.5rem;
      margin-right: 0.2rem;
    }
  }
.search-header-4{

  width:100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  position: fixed;
  top: 0;
  left: 0;
  .search-xBox{
    width:70%;
    background:#C9C9C9;
    border-radius: 5px;
    margin:4.5rem 0.4rem;
    position: relative;
    z-index: 999;
    ul{
      display: flex;
      li{
        padding: 0.5rem 0 0 0.2rem;
        text-align: center;
        margin-left: 1rem;
        a{
          display: inline-block;
          font-size: 0.7rem;
          color: black;
          padding-bottom: 0.5rem;
        }
        img{
          width:2rem;
          height: 2rem;
        }
      }
      li:nth-of-type(1){
        padding: 0.5rem 0 0 0rem;
      }
    }
    span{
      position: absolute;
      color:#C9C9C9;
      font-size: 0.5rem;
      left: 0.3rem;
      top: -0.4rem;
    }
  }
}
</style>



