<template>
  <div class="">
    <div class="search-header-1">
      <router-link to="fanHui" class="iconfont">&#xf016e;</router-link>
      <h2>频道管理</h2>
    </div>
    <p class="removeChannel">点击删除一下频道</p>
    <ul class="rChannel clearfix">
      <li v-for="(rae,index) in rChannel" @click="removeList(index)">
        {{ rae }}
      </li>
    </ul>
    <p class="addChannel">点击添加一下频道</p>
    <ul class="rChannel">
      <li v-for="(raeadd,index) in addChannel" @click="addList(index)">
        {{ raeadd }}
      </li>
    </ul>
  </div>

</template>

<script>
  export default {
    data(){
      return {
        rChannel: [
          "推荐", "科技", "体育","军事","国际","时尚","游戏","旅游","探索",
        ],
        addChannel:[
          "热点","社会","娱乐","汽车","财经","历史","美食","育儿","养生","故事","美文",
        ]
      }
    },
    methods:{
      removeList(index){
          this. addChannel.push(this.rChannel[index]);
          this.rChannel.splice(index,1)
      },
      addList(index){
        this.rChannel.push(this. addChannel[index]);
        this.addChannel.splice(index,1)
      }
    }
  }
</script>

<style scoped lang="scss">
  .search-header-1 {
    width: 100%;
    height: 2.2rem;
    background: #D63C39;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    a {
      position: absolute;
      top: 0.5rem;
      left: .5rem;
      color: white;
      font-size: 1rem;
    }
    h2 {
      color: white;
      font-size: 1rem;
      font-weight: bold;
    }
  }
  .rChannel{
    li{
      width:3rem;
      height: 1.3rem;
      line-height: 1.3rem;
      border: 1px solid #7e8c8d;
      margin: .5rem 0 0 0.8rem;
      font-size: 0.8rem;
      float: left;
      text-align: center;
    }
  }
  .addChannel{
    font-size: .6rem;
    padding: .3rem 0 0 .5rem;
    margin-top: 1rem;
  }
  .removeChannel {
    font-size: .6rem;
    padding: .3rem 0 0 .5rem;
  }
  .clearfix:after{
    display: block;
    clear: both;
    content:'';
  }
</style>




