import Vue from 'vue'
import Router from 'vue-router'
import header from '@/components/header'
import tuiJian from '@/components/tuiJian/tuiJian'
import video from '@/components/video/video'
import reDian from '@/components/reDian/reDian'
import sheHui from '@/components/sheHui/sheHui'
import yuLe from '@/components/yuLe/yuLe'
import junShi from '@/components/junShi/junShi'
import search from '@/components/search/search'
import append from '@/components/append/append'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'header',
      component: header,
      children: [
        {
          path: '/',
          name: 'tuiJian',
          component: tuiJian
        },
        {
          path: '/video',
          name: 'video',
          component: video
        },
        {
          path: '/reDian',
          name: 'reDian',
          component: reDian
        },
        {
          path: '/sheHui',
          name: 'sheHui',
          component: sheHui
        },
        {
          path: '/yuLe',
          name: 'yuLe',
          component: yuLe
        },
        {
          path: '/junShi',
          name: 'junShi',
          component: junShi
        }
      ]
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/fanHui',
      name: 'header',
      component: header
    },
    {
      path: '/append',
      name: 'append',
      component: append
    }

  ]
})



